/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700;1,900&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

*, *::before, *::after {
  box-sizing: border-box; }

.white {
  color: #fff; }

.bg-black {
  background-color: #000; }

/* space */
.mb-1 {
  margin-bottom: 4px; }

.mb-2 {
  margin-bottom: 8px; }

.mb-3 {
  margin-bottom: 12px; }

.mb-4 {
  margin-bottom: 16px; }

.mb-5 {
  margin-bottom: 20px; }

.mb-6 {
  margin-bottom: 24px; }

.mr-6 {
  margin-right: 16px; }

.mb-7 {
  margin-bottom: 28px; }

.mb-8 {
  margin-bottom: 32px; }

.mb-10 {
  margin-bottom: 40px; }

.mb-12 {
  margin-bottom: 48px; }

/* font */
.text-s {
  font-size: 20px;
  line-height: 30px;
  font-weight: 500; }

.text-m {
  font-size: 20px;
  line-height: 42px;
  font-weight: 500; }

.subTitle-s {
  font-size: 24px;
  line-height: 36px; }

.subTitle-m {
  font-size: 24px;
  line-height: 42px; }

.title {
  font-size: 64px;
  line-height: 84px;
  font-weight: 500; }

.fw-md {
  font-weight: 500; }

.fw-bold {
  font-weight: 700; }

.fw-lg {
  font-weight: 900; }

.no-italic {
  font-style: normal; }

body {
  font-family: "Roboto", "sans-serif";
  font-style: italic; }

a:hover {
  opacity: 0.7; }

.header {
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week2/banner.png?raw=true);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100vh; }
  .header__container {
    width: 304px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    .header__container h1 {
      font-size: 80px;
      line-height: 106px;
      font-weight: 900; }
    .header__container span {
      display: block;
      text-align-last: left; }
  .header__content {
    width: 283px;
    margin: 0 auto; }
  .header__links {
    display: flex;
    justify-content: space-between; }
    .header__links a {
      display: block;
      width: 60px;
      height: 60px; }

.profile__container {
  width: 384px;
  padding: 56px 0;
  margin: 0 auto; }
  .profile__container img {
    margin-left: 14px;
    margin-right: 14px; }
  .profile__container h2 {
    text-align: center; }

.profile__content {
  text-align: center; }
  .profile__content li {
    margin-left: -14px;
    margin-right: -14px; }

.education__list {
  border-top: 1px solid #000;
  padding-top: 28px;
  text-align: center; }
  .education__list li {
    display: flex;
    justify-content: center;
    align-items: center; }
  .education__list span {
    position: relative; }
    .education__list span::afteR {
      content: '';
      background-color: #891818;
      width: 22px;
      height: 22px;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: -2rem; }

.brand {
  padding: 56px 0; }
  .brand__content {
    width: 500px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 68px; }
  .brand__container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }

.works {
  width: 1200px;
  margin: 0 auto;
  text-align: center;
  padding: 56px 0; }
  .works__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 56px; }
    .works__list a {
      display: block; }

.footer {
  padding: 64px 0;
  margin: 0 auto;
  text-align: center; }
  .footer a {
    text-decoration: none; }
